const loading = (
  state = {
    isLoading: "initial",
    loadingProgress: 0,
    loadingStatus:
      "Moving symbol to library please wait it will take some time 🐙"
  },
  action
) => {
  switch (action.type) {
    case "START_LOADING":
      return (state = {
        ...state,
        isLoading: "loading"
      });
    case "ERROR":
      return (state = {
        ...state,
        isLoading: "error"
      });
    case "SUCESS":
      return (state = {
        ...state,
        isLoading: "success",
        loadingStatus: action.payload
      });
    case "LOADING_CHANGE":
      return (state = {
        ...state,
        loadingProgress: action.payload.progress,
        loadingStatus: action.payload.message
      });
    default:
      return state;
  }
};
export default loading;



