import React from "react";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import pluginCall from "sketch-module-web-view/client";

const styles = theme => ({
  container: {
    display: "grid",
    gridAutoRows: "80px 32px 58px",
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  header: {
    color: "white",
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    textAlign: "center",
    fontSize: "1.5rem"
  },
  paragraph: {
    color: "rgba(255, 255, 255, 0.8)",
    fontSize: "1rem",
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    textAlign: "center"
  },
  paragraph2: {
    color: "rgba(255, 255, 255, 1)",
    fontSize: "0.8rem",
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    textAlign: "center",
    margin: "0"
  },
  buttonContainer: {
    padding: "10px",
    background: "#c1888c61",
    borderRadius: "5px",
    height: "max-content"
  },
  button: {
    marginTop: "10px",
    background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)",
    borderRadius: 3,
    border: 0,
    color: "white",
    height: 48,
    padding: "0 30px",
    boxShadow: "0 0px 3px 1px rgba(255, 105, 135, .30)",
    "&:hover": {
      boxShadow: "0 2px 5px 2px rgba(255, 105, 135, .30)",
      background: "linear-gradient(45deg, #FE6B8B 60%, #FF8E53 90%)",
      "-webkit-transition": "background 1000ms ease-in",
      "-ms-transition": "background 1000ms ease-in",
      transition: "background 1000ms ease-in"
    }
  }
});

const statePage = props => {
  const { classes } = props;
  function clickHandler() {
    pluginCall("openURL", "https://www.paypal.me/Genaidy");
  }
  return (
    <div className={classes.container}>
      <svg
        id="successAnimation"
        className="animated"
        xmlns="http://www.w3.org/2000/svg"
        width="70"
        height="70"
        viewBox="0 0 70 70"
      >
        <path
          id="successAnimationResult"
          fill="#D8D8D8"
          d="M35,60 C21.1928813,60 10,48.8071187 10,35 C10,21.1928813 21.1928813,10 35,10 C48.8071187,10 60,21.1928813 60,35 C60,48.8071187 48.8071187,60 35,60 Z M23.6332378,33.2260427 L22.3667622,34.7739573 L34.1433655,44.40936 L47.776114,27.6305926 L46.223886,26.3694074 L33.8566345,41.59064 L23.6332378,33.2260427 Z"
        />
        <circle
          id="successAnimationCircle"
          cx="35"
          cy="35"
          r="24"
          stroke="#979797"
          strokeWidth="2"
          strokeLinecap="round"
          fill="transparent"
        />
        <polyline
          id="successAnimationCheck"
          stroke="#979797"
          strokeWidth="2"
          points="23 34 34 43 47 27"
          fill="transparent"
        />
      </svg>
      <h1 className={classes.header}>Your moved to library successfully</h1>
      <p className={classes.paragraph}>{props.message}</p>
      <div className={classes.buttonContainer}>
        <p className={classes.paragraph2}>
          This project is completely free,5€ Supporting us to keep updating the
          plugin 🙌.
        </p>
        <Button
          className={classes.button}
          fullWidth
          onClick={() => clickHandler()}
        >
          Support Us via Paypal
        </Button>
      </div>
    </div>
  );
};

export default withStyles(styles)(statePage);



