import React from "react";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import pluginCall from "sketch-module-web-view/client";

const styles = theme => ({
  container: {
    display: "grid",
    gridAutoRows: "38% 20% auto",
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  emoji: {
    fontSize: "5rem",
    margin: "auto"
  },
  header: {
    color: "white",
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    textAlign: "center",
    fontSize: "1.5rem"
  },
  paragraph: {
    color: "rgba(255, 255, 255, 0.8)",
    fontSize: "1rem",
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    textAlign: "center"
  },
  paragraph2: {
    color: "rgba(255, 255, 255, 0.8)",
    fontSize: "1rem",
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    textAlign: "center",
    margin: "0"
  },
  button: {
    marginTop: "10px",
    background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)",
    borderRadius: 3,
    border: 0,
    color: "white",
    height: 48,
    padding: "0 30px",
    boxShadow: "0 0px 3px 1px rgba(255, 105, 135, .30)",
    "&:hover": {
      boxShadow: "0 2px 5px 2px rgba(255, 105, 135, .30)",
      background: "linear-gradient(45deg, #FE6B8B 60%, #FF8E53 90%)",
      "-webkit-transition": "background 1000ms ease-in",
      "-ms-transition": "background 1000ms ease-in",
      transition: "background 1000ms ease-in"
    }
  }
});

const statePageError = props => {
  const { classes } = props;
  function clickHandler() {
    pluginCall(
      "openURL",
      "https://github.com/ahmedmigo/Move-to-library-sketchplugin/issues"
    );
  }
  return (
    <div className={classes.container}>
      <span className={classes.emoji}> 😥 </span>
      <h1 className={classes.header}>Sorry Something went wrong!</h1>
      <p className={classes.paragraph}>Please try again, or contact us</p>
      <div>
        <Button
          className={classes.button}
          fullWidth
          onClick={() => clickHandler()}
        >
          Contact us
        </Button>
      </div>
    </div>
  );
};

export default withStyles(styles)(statePageError);



