import React, { Component } from "react";
import { connect } from "react-redux";
import { withStyles } from "@material-ui/core/styles";
import "../App.css";
import Dropdown from "./dropdown";
import Loader from "./loader";
import StatePage from "./statePage";
import StatePageError from "./statePageError";
import pluginCall from "sketch-module-web-view/client";
import FundTile from "./fundtile";

const styles = theme => ({
  container: {
    display: "grid",
    height: "100vh",
    width: "100vw",
    gridAutoRows: "112px auto 100px 24px"
  },
  header: {
    background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)",
    color: "white",
    fontSize: "2rem",
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    padding: "5px",
    paddingLeft: "15px",
    paddingTop: "60px"
  },
  fundTile: {},
  footer: {
    background: "black",
    color: "rgba(255, 255, 255, 0.54)",
    fontSize: "0.7rem",
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    padding: "5px",
    paddingLeft: "8px"
  },
  a: {
    color: "#FE6B8B",
    cursor: "pointer"
  }
});

class App extends Component {
  setBody(loadingState) {
    switch (loadingState.isLoading) {
      case "initial":
        return <Dropdown />;
      case "loading":
        return <Loader />;
      case "success":
        return <StatePage message={loadingState.loadingStatus} />;
      case "error":
        return <StatePageError />;
      default:
        return;
    }
  }

  render() {
    const { classes } = this.props;
    function clickHandler() {
      pluginCall("openURL", "https://twitter.com/ser_migo");
    }
    return (
      <div className={classes.container}>
        <div className={classes.header}> Move to library </div>
        {this.setBody(this.props.loading)}
        <FundTile />
        <div className={classes.footer}>
          Created by
          <a className={classes.a} onClick={() => clickHandler()}>
            {" "}
            @ser_migo
          </a>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    loading: state.loading
  };
};

export default connect(mapStateToProps)(withStyles(styles)(App));



