const libraries = (
  state = {
    libraries: [
      {
        value: "-1",
        label: "No library found"
      }
    ],
    selectedLibrary: {
      value: "-1",
      label: "No library found"
    }
  },
  action
) => {
  switch (action.type) {
    case "LOAD_LIBRAIES":
      var selectedlibrarycashe = readCookie("selectedlibrary");
      if (
        !selectedlibrarycashe ||
        action.payload[selectedlibrarycashe] === undefined
      ) {
        selectedlibrarycashe = 0;
      }
      return (state = {
        ...state,
        libraries: action.payload,
        selectedLibrary: action.payload[selectedlibrarycashe]
      });
    case "SELECT_LIBRARY":
      return (state = {
        ...state,
        selectedLibrary: state.libraries[action.payload]
      });
    default:
      return state;
  }
};

function readCookie(name) {
  var nameEQ = name + "=";
  var ca = document.cookie.split(";");
  for (var i = 0; i < ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) == " ") c = c.substring(1, c.length);
    if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
  }
  return null;
}

export default libraries;



