export function startLoading() {
  return {
    type: "START_LOADING"
  };
}

export function errorLoading() {
  return {
    type: "ERROR"
  };
}

export function successLoading(message) {
  return {
    type: "SUCESS",
    payload: message
  };
}

export function changeLoading(progress, message) {
  return {
    type: "LOADING_CHANGE",
    payload: {
      progress,
      message
    }
  };
}



