import React from "react";
import { render } from "react-dom";
import "./index.css";
import App from "./components/App";
import registerServiceWorker from "./registerServiceWorker";
import { Provider } from "react-redux";
import { setLibraries } from "./Actions/librariesAction";
import {
  changeLoading,
  startLoading,
  errorLoading,
  successLoading
} from "./Actions/loadingAction";

import store from "./store";

window.getLibraries = function(libraries) {
  store.dispatch(setLibraries(libraries));
};

window.changeLoading = function(progress, message) {
  store.dispatch(changeLoading(progress, message));
};

window.startLoading = function() {
  store.dispatch(startLoading());
};

window.errorLoading = function() {
  store.dispatch(errorLoading());
};

window.successLoading = function(message) {
  store.dispatch(successLoading(message));
};

render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById("root")
);

registerServiceWorker();



