import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from "@material-ui/core/TextField";
import "typeface-roboto";
import pluginCall from "sketch-module-web-view/client";
import Button from "@material-ui/core/Button";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import { selectLibrary } from "../Actions/librariesAction";
import { startLoading } from "../Actions/loadingAction";

const theme = createMuiTheme({
  overrides: {
    // Name of the component ⚛️ / style shee
    MuiFormLabel: {
      // Name of the rule
      root: {
        // Some CSS
        color: "rgba(255, 255, 255, 0.54)",
        "&$focused": {
          color: "#FE6B8B"
        }
      }
    },
    MuiSelect: {
      icon: {
        color: "rgba(255, 255, 255, 0.54)"
      }
    },
    MuiInput: {
      root: {
        color: "rgba(255, 255, 255, 1)"
      },
      underline: {
        "&:before": {
          borderBottom: "1px solid rgba(255, 255, 255, 0.54)"
        },
        "&:after": {
          borderBottom: "1px solid #FE6B8B"
        }
      }
    },
    MuiFormHelperText: {
      root: {
        color: "rgba(255, 255, 255, 0.54)"
      }
    }
  }
});

const styles = theme => ({
  container: {
    display: "flex",
    flexWrap: "wrap",
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  textField: {},
  menu: {
    width: 200
  },
  button: {
    marginTop: "10px",
    background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)",
    borderRadius: 3,
    border: 0,
    color: "white",
    height: 48,
    padding: "0 30px",
    boxShadow: "0 0px 3px 1px rgba(255, 105, 135, .30)",
    "&:hover": {
      boxShadow: "0 2px 5px 2px rgba(255, 105, 135, .30)",
      background: "linear-gradient(45deg, #FE6B8B 60%, #FF8E53 90%)",
      "-webkit-transition": "background 1000ms ease-in",
      "-ms-transition": "background 1000ms ease-in",
      transition: "background 1000ms ease-in"
    }
  }
});

class TextFields extends React.Component {
  constructor(props) {
    super(props);
    this.clickHandler = this.clickHandler.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange = event => {
    this.props.selectLibrary(event.target.value);
    createCookie("selectedlibrary", event.target.value, 30);
    console.log(event.target.value);
    console.log(readCookie("selectedlibrary"));
  };

  clickHandler() {
    // this.props.startLoading();
    // setTimeout(() => {

    // }, 100);
    try {
      pluginCall("setLibrary", this.props.libraries.selectedLibrary.value);
    } catch (err) {
      console.log(err);
    }
    console.log(this.props.libraries.selectedLibrary.label);
  }

  render() {
    const { classes } = this.props;

    return (
      <MuiThemeProvider theme={theme}>
        <form className={classes.container} noValidate autoComplete="off">
          <TextField
            id="select-library"
            select
            label="Select"
            className={classes.textField}
            value={this.props.libraries.selectedLibrary.value}
            onChange={this.handleChange}
            SelectProps={{
              MenuProps: {
                className: classes.menu
              }
            }}
            helperText="Please select your library"
            fullWidth
            margin="normal"
          >
            {this.props.libraries.libraries.map(option => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
          <Button
            className={classes.button}
            fullWidth
            onClick={this.clickHandler}
          >
            Move to library
          </Button>
        </form>
      </MuiThemeProvider>
    );
  }
}

TextFields.propTypes = {
  classes: PropTypes.object.isRequired
};

const mapStateToProps = state => {
  return {
    libraries: state.libraries
  };
};
const mapDispatchToProps = dispatch => {
  return {
    selectLibrary: library => {
      dispatch(selectLibrary(library));
    },
    startLoading: () => {
      dispatch(startLoading());
    }
  };
};

function createCookie(name, value, days) {
  if (days) {
    var date = new Date();
    date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
    var expires = "; expires=" + date.toGMTString();
  } else var expires = "";
  document.cookie = name + "=" + value + expires + "; path=/";
}

function readCookie(name) {
  var nameEQ = name + "=";
  var ca = document.cookie.split(";");
  for (var i = 0; i < ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) == " ") c = c.substring(1, c.length);
    if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
  }
  return null;
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(TextFields));



